.artwork-card {
    border: 1px solid #ddd;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    background-color: white;
  }
  
.artwork-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
}

  
  .artwork-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .artwork-details {
    padding: 15px;
  }
  
  .artwork-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .artwork-artist {
    font-size: 14px;
    color: #888;
  }
  
  .artwork-price {
    color: #ff5a5f;
    font-size: 16px;
  }
  