/* Navbar styles */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 30px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

/* Navbar Logo */
.navbar-logo a {
  font-size: 24px;
  font-weight: bold;
  color: #ff5a5f;
  text-decoration: none;
}

/* Center the search bar */
.navbar-center {
  flex: 1;
  display: flex;
  justify-content: center;
}

/* Navbar Links */
.navbar-links {
  display: flex;
  justify-content: flex-end;
  
}

.navbar-links a {
  margin-left: 20px;
  font-size: 16px;
  color: #333;
  text-decoration: none;
  font-weight: 500;
}

.navbar-links a:hover {
  color: #ff5a5f;
}

/* Fixed navigation items below the navbar */
.navbar-items {
  display: flex;
  justify-content: center;
  padding: 10px 0;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: fixed; /* Fixed positioning */
  top: 83px; /* Adjust to fit below the navbar */
  left: 0;
  right: 0;
  z-index: 999; /* Ensure it stays above content */
  font-family: italic;
}

.navbar-items a {
  margin: 0 15px;
  font-size: 18px;
  color: #333;
  text-decoration: none;
}

.navbar-items a:hover {
  font-weight: bold;
  text-shadow: black;
  color: #ff5a5f;
  border-bottom: 2px solid #ff5a5f;
}

/* Add top padding to the content below the fixed navbar items */
.main-content {
  padding-top: 130px; /* Adjust based on the combined height of the navbar and fixed navigation items */
}
